.wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.container{
    width: var(--default-layout-width);
    min-height: 1000px;
    display: flex;
}
.content{
    flex:1;
    background-image: linear-gradient(#c6ffdd, #fbd786, #f7797d);
}