.wrapper {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 100px;
    padding: 9px 16px;
    border-radius: 4px;
    font-size: 1.6rem;
    font-weight: 700;
    cursor: pointer;
    // background-color: transparent;
    border: 1px solid transparent;
    user-select: none;

    + .wrapper {
        margin-left: 12px;
    }

    &.disable {
        pointer-events: none;
        opacity: 0.5;
    }
}

//Button types
.rounded {
    border-radius: 999px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
    border-color: rgba(22, 24, 35, 0.12);

    background-color: rgba(22, 24, 35, 0.01);
    &:hover {
        background-color: rgba(22, 24, 35, 0.05);
        border-color: rgba(22, 24, 35, 0.2);
    }
}

.primary {
    background-color: var(--primary);
    color: var(--white);

    &:hover {
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), var(--primary);
        background-color: var(--primary);
    }
}

.outline {
    color: var(--primary);
    background-color: transparent;
    border-color: currentColor;
    &:hover {
        background-color: rgba(254, 44, 85, 0.6);
        border-color: currentColor;
    }
}

.text {
    background-color: transparent;
    &:hover {
        text-decoration: underline;
    }
}


.icon + .title , .title + .icon {
    margin-left: 8px;
}

//Button sizes
.small {
    min-width: 88px;
    padding: 4px, 16px;
}

.large {
    min-width: 140px;
    padding: 14px, 16px;
}
